import clsx from "clsx";
import React, { useState } from "react";
import "./NavBar.scss";
import { NavBarProps } from "./NavBar.types";

const NavBar: React.FC<NavBarProps> = ({
  isLoggedIn,
  hideConnectWalletDropdown,
  navLinks,
}) => {
  const [mobileNavOpen, setMobileNavOpen] = useState(false);

  const hideMobileNav = () => {
    setMobileNavOpen(false);
  };

  return (
    <>
      <header>
        <a className="visually-hidden focusable" href="#skip-to-main">
          Skip navigation
        </a>
        <nav role="navigation" className={clsx(mobileNavOpen && "open")}>
          <a className="logo" href="/">
            <img
              style={{
                objectFit: "cover",
                minWidth: "143px",
                minHeight: "28px",
              }}
              alt="Code4rena Logo"
              src="/c4-logo.svg"
              width={143}
              height={28}
              loading="lazy"
            />
          </a>
          <button
            className="menu--hamburger"
            onClick={() => setMobileNavOpen((isOpen: boolean) => !isOpen)}
            aria-label={mobileNavOpen ? "Close menu" : "Open menu"}
            aria-expanded={mobileNavOpen}
          >
            <svg
              height="22"
              viewBox="0 0 22 22"
              width="22"
              xmlns="http://www.w3.org/2000/svg"
            >
              <g fill="#d8d8d8" fillRule="evenodd">
                <rect className="top-bun" height="2" rx="1.5" width="22" />
                <rect className="patty" height="2" rx="1.5" width="22" y="9" />
                <rect
                  className="bottom-bun"
                  height="2"
                  rx="1.5"
                  width="22"
                  y="18"
                />
              </g>
            </svg>
          </button>
          <div className="nav--links">
            {navLinks?.map((link) => {
              return (
                <a
                  key={`nav_link_${link.label}`}
                  href={link.href}
                  target={link.external ? "_blank" : undefined}
                  rel={link.external ? "noreferrer noopener" : undefined}
                >
                  {link.label}
                </a>
              );
            })}
            <div className="nav--buttons">
              {!hideConnectWalletDropdown &&
                (isLoggedIn ? <UserDropdown /> : <Login />)}
            </div>
          </div>
        </nav>
      </header>
      <span id="skip-to-main" />
    </>
  );
};

NavBar.defaultProps = {
  isLoggedIn: false,
  hideConnectWalletDropdown: false,
  navLinks: [],
};
export default NavBar;
